.homePage{
  min-height: 100vh;
  background-color: rgba(51, 45, 45, 0.9);
  display: flex;
  color: white;
}
.question {
  height: 20vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.box {
  display: flex;
  align-items: center;
  justify-content: center;
}
button {
  background-color: transparent;
  text-transform: uppercase;
  color: rgba(230, 154, 14, 1);
  border: none;
  border: 1px solid rgba(230, 154, 14, 1);
  border-radius: 5px;
  padding: 15px;
  margin-bottom: 10px;
  width: 100%;
  cursor: pointer;
  transition: all 0.5s ease;
  &:hover {
    background-color: rgba(230, 154, 14, 0.8);
    border: 1px solid black;
    color: black;
  }
}